/* HomePage.css */
.homepage-container {
    min-height: 100vh;
    background: linear-gradient(to bottom right, #20c997, #0c7b93, black);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.header-section {
    width: 100%;
    max-width: 64rem;
    text-align: center;
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.header-title {
    font-size: 3.75rem;
    font-weight: 800;
    background: linear-gradient(to right, #005f68, #c800a9);
    -webkit-background-clip: text;
    color: transparent;
    text-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.header-description {
    font-size: 1.25rem;
    margin-top: 1rem;
    max-width: 40rem;
    margin-left: auto;
    margin-right: auto;
    color: #d1d5db;
}

.projects-section {
    width: 100%;
    max-width: 64rem;
}

.section-title {
    font-size: 1.875rem;
    font-weight: 600;
    margin-bottom: 1rem;
}

.project-item {
    margin-bottom: 2rem;
}

.project-title {
    font-size: 1.25rem;
    font-weight: 600;
}

.project-description {
    font-size: 1.125rem;
    margin-bottom: 0.5rem;
}

.project-video {
    width: 100%;
    border-radius: 0.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.contact-section {
    margin-top: 2.5rem;
    width: 100%;
    max-width: 64rem;
}

.contact-description {
    font-size: 1.125rem;
}

.contact-list {
    font-size: 1.125rem;
    margin-top: 0.5rem;
}

.contact-link {
    text-decoration: underline;
}

.footer {
    margin-top: 2.5rem;
    text-align: center;
    font-size: 0.875rem;
    color: #d1d5db;
}